import {
  _getUsers,
  _getQuestions,
  _saveQuestion,
  _saveQuestionAnswer
} from './_DATA.js'

export function getInitialData() {
  return Promise.all([
    _getUsers(),
    _getQuestions(),
  ]).then(([users, questions]) => ({
    users,
    questions
  }))
}

// info must include author, optionOneText, optionTwoText
export function saveQuestion(info) {
  return _saveQuestion(info)
}

// info must include authedUser, qid, answer
// answer is either 'optionOne' or 'optionTwo'
export function saveQuestionAnswer(info) {
  return _saveQuestionAnswer(info)
}
